import React from 'react';
import Slider from 'react-slick';

import 'aos/dist/aos.css';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../styles/carousel.css';

class ProjectsCarousel extends React.Component {
  render() {

    let settings = {
      swipeToSlide: true,
      autoplay: true,
      infinite: true,
      slidesToShow: 3,
      slidesToScroll: 1,
      pauseOnHover: true,
      autoplaySpeed: 9000,
      dots: true,
      // adaptiveHeight: true,
      // adaptiveWidth: true, 

    };

    return (
      <div className='container'>
        <Slider {...settings} className='carousel'>
          <div className=''>
            <img className='slider-img'src='https://source.unsplash.com/400x400/?nature,dogs' alt="My Reads app Image"/>
            <a href="https://youtube.com">DealQuest App</a>
          </div>
          <div className=''>
            <img className='slider-img' src='https://source.unsplash.com/400x400/?nature,cows' alt="My Reads app Image"/>
            <a href="https://youtube.com">DealQuest App</a>
          </div>
          <div className=''>
            <img className='slider-img' src='https://source.unsplash.com/400x400/?nature,landscape' alt="My Reads app Image"/>
            <a href="https://youtube.com">DealQuest App</a>
          </div>
          <div className=''>
            <img className='slider-img' src='https://source.unsplash.com/400x400/?nature,ocean' alt="My Reads app Image"/>
            <a href="https://youtube.com">DealQuest App</a>
          </div>

        </Slider>
      </div>
    );
  }
}

export default ProjectsCarousel;


